@import "../base/_variable.less";
main{
    .about-me{
        background: rgb(195,254,255);
        background: linear-gradient(126deg, rgb(203, 246, 247) 0%, rgba(218,204,252,1) 34%, rgb(225, 160, 235) 64%, rgb(228, 192, 115) 100%);
        margin-top: 70px;
        padding: @main-padding;
        &__container{
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-template-rows: repeat(2, 300px);
            gap: 2rem;
            .about-me__text{
                grid-column: 1 / 1 span;
                grid-row: 1 / 1 span;
                background-color: red;
                display: flex;
                flex-direction: column;
                gap: 1rem;
                .about-me__text__heading{
                    width: 55%;
                    font-size: clamp(1.5rem, 1.3rem + 1.0667vw, 2.5rem);
                }
                .about-me__text__para{
                    font-size: clamp(1rem, 0.95rem + 0.2667vw, 1.25rem);                    line-height: 1.5rem;
                }
            }
            &__statics{
                background-color: green;
                grid-column: 1 / 1 span;
                grid-row: 2 / 1 span;
            }
            &__image{
                background-color: grey;
                grid-column: 2 / 1 span;
                grid-row: 1 / 2 span;
                img{
                    width: 100%;
                    height: auto;
                    object-fit: cover;
                }
            }
        }
    }
}


